import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Firm from "../views/Firm.vue";
import Services from "../views/Services.vue";
import SingleService from "../views/SingleService.vue";
import Contact from "../views/Contact.vue";


Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/firm",
    name: "Firm",
    component: Firm,
  },
  {
    path: "/practical-areas",
    name: "Services",
    component: Services,
  },
  {
    path: "/practical-area/:id",
    name: "SingleService",
    component: SingleService,
    props: true
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact,
    props: true
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
